.site-search {
  display:flex;
  flex-direction: row;
  align-items: center;
  height:2em;
  width:3.75em;
  padding:0.5em;
  border-radius:1em;
  font-size:1em;
  border:1px solid #dbdddf;
  box-shadow: inset 0 3px 10px #eceef0;
  margin-right:2em;
  transition: width 0.3s;
  margin-left: 1em;
}

.site-search.large {
  width:12em;
}

.site-search img {
  height:1em;
}

.site-search input {
  -webkit-appearance: none;
  display:block;
  height:2em;
  padding:0.5em;
  width: 100%;
  font-size:0.85em;
  border:0;
  outline:0;
  background:transparent;
  color: transparent;
}

.site-search.large input {
  color: initial;
}
